<template>
  <div class="container register-wrap">
    <div class="wrap-center">
      <step-header style="width: 478px;margin: 0  auto" :stepIndex="stepIndex" :stepList="stepList"></step-header>
      <div class="title">企业认证</div>
      <el-form label-width="145px" class="dataForm">
        <!-- 111111 -->
        <div class="sub-title">
          企业相关信息
        </div>
        <div class="form-step">
          <el-form-item label="证件类型">
            <el-radio-group v-model="dataForm.type" class="block-radio">
              <el-radio label="1">统一社会信用代码（三证合一营业执照）</el-radio>
              <el-radio label="2">其他类型（对公或其他）</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="证件代码">
            <el-input placeholder="请输入证件代码"></el-input>
          </el-form-item>
          <el-form-item label="企业名称">
            <el-input placeholder="请输入企业名称"></el-input>
          </el-form-item>
          <el-form-item label="企业简称">
            <el-input placeholder="请输入企业简称"></el-input>
          </el-form-item>
          <el-form-item label="网站地址">
              <el-input placeholder="请输入网站地址">
                <template slot="prepend">Http://</template>
              </el-input>
          </el-form-item>
          <el-form-item label="单位所在地" class="notSetLong">
            <div style="display: flex; width: 468px; justify-content: space-between;">
              <el-select v-model="dataForm.region" placeholder="请选择省">
                <el-option label="区域一" value="shanghai"></el-option>
                <el-option label="区域二" value="beijing"></el-option>
              </el-select>
              <el-select v-model="dataForm.region1" placeholder="请选择城市">
                <el-option label="区域一" value="shanghai"></el-option>
                <el-option label="区域二" value="beijing"></el-option>
              </el-select>
            </div>
          </el-form-item>
          <el-form-item label="详细地址">
            <el-input placeholder="请输入详细地址"></el-input>
          </el-form-item>
          <el-form-item label="经营范围">
            <el-input placeholder="请输入经营范围"></el-input>
          </el-form-item>
          <el-form-item label="经营期限">
            <el-date-picker
              style="width: 468px"
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
            <el-checkbox v-model="checked" fill="#B90909">长期</el-checkbox>
          </el-form-item>
        </div>
         <!-- 222222 -->
        <div class="sub-title">
          法人信息
        </div>
        <div  class="form-step">
          <el-form-item label="法定代表人姓名">
            <el-input placeholder="请输入法定代表人姓名"></el-input>
          </el-form-item>
          <el-form-item label="国籍">
            <el-input placeholder="请输入国籍"></el-input>
          </el-form-item>
          <el-form-item label="性别">
            <el-radio-group v-model="dataForm.sex">
              <el-radio label="男"></el-radio>
              <el-radio label="女"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="法定代表人身份证号">
            <el-input placeholder="请输入法定代表人身份证号"></el-input>
          </el-form-item>
          <el-form-item label="证件有效期">
            <el-date-picker
              style="width: 468px"
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
            <el-checkbox v-model="checked" fill="#B90909">长期</el-checkbox>
          </el-form-item>
          <el-form-item label="法人手机号">
            <el-input placeholder="请输入法人手机号"></el-input>
          </el-form-item>
          <el-form-item label="职业">
            <el-input placeholder="请输入法人职业"></el-input>
          </el-form-item>
          <el-form-item label="联系地址">
            <el-input placeholder="请输入法人联系地址"></el-input>
          </el-form-item>
          <el-form-item label="填写人身份">
            <el-radio-group v-model="dataForm.sex">
              <el-radio label="我是法定代表人"></el-radio>
              <el-radio label="我是代理人"></el-radio>
            </el-radio-group>
          </el-form-item>
        </div>
        <!-- 333333 -->
        <div class="sub-title">
          实际控制人信息
        </div>
        <div  class="form-step">
          <el-form-item label="实际控制人性质">
            <el-radio-group v-model="dataForm.sex">
              <el-radio label="自然人"></el-radio>
              <el-radio label="非自然人"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="实际控制人归属地">
            <el-input placeholder="请输入实际控制人归属地"></el-input>
          </el-form-item>
          <el-form-item label="实际控制人姓名">
            <el-input placeholder="请输入实际控制人姓名"></el-input>
          </el-form-item>
          <el-form-item label="实际控制人身份证号">
            <el-input placeholder="请输入实际控制人身份证号"></el-input>
          </el-form-item>
          <el-form-item label="证件有效期">
            <el-date-picker
              style="width: 468px"
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
            <el-checkbox v-model="checked" fill="#B90909">长期</el-checkbox>
          </el-form-item>
          <el-form-item label="填写人身份">
            <el-radio-group v-model="dataForm.sex">
              <el-radio label="我是法定代表人"></el-radio>
              <el-radio label="我是代理人"></el-radio>
            </el-radio-group>
          </el-form-item>
        </div>
        <!-- 444444 -->
        <div class="sub-title">
          联系人信息
        </div>
        <div  class="form-step">
          <el-form-item label="联系人姓名">
            <el-input placeholder="请输入联系人姓名"></el-input>
          </el-form-item>
           <el-form-item label="国籍">
            <el-input placeholder="请输入国籍"></el-input>
          </el-form-item>
          <el-form-item label="性别">
            <el-radio-group v-model="dataForm.sex">
              <el-radio label="男"></el-radio>
              <el-radio label="女"></el-radio>
            </el-radio-group>
          </el-form-item>
           <el-form-item label="联系人身份证号">
            <el-input placeholder="请输入联系人身份证号"></el-input>
          </el-form-item>
          <el-form-item label="证件有效期">
             <el-date-picker
              style="width: 468px"
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
            <el-checkbox v-model="checked" fill="#B90909">长期</el-checkbox>
          </el-form-item>
           <el-form-item label="职业">
            <el-input placeholder="请输入联系人职业"></el-input>
          </el-form-item>
          <el-form-item label="联系地址">
            <el-input placeholder="请输入联系地址"></el-input>
          </el-form-item>
          <el-form-item label="手机号码">
            <el-input placeholder="请输入手机号码" style="width: 315px"></el-input>
            <button type="button" class="yzm" :class="{'disabled': disabled}" @click="getYzm" :disabled="disabled">{{yzmText}}</button>
          </el-form-item>
           <el-form-item label="验证码">
            <el-input placeholder="请输入验证码"></el-input>
          </el-form-item>
        </div>
        <!-- 555555 -->
        
        <div class="sub-title">
          浦发联系人信息
        </div>
        <div  class="form-step">
          <el-form-item label="认证原因">
            <el-radio-group v-model="dataForm.sex">
              <el-radio label="接受邀请"></el-radio>
              <el-radio label="主动邀请"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="浦发联系人所属机构">
            <el-input placeholder="请输入浦发联系人所属机构"></el-input>
          </el-form-item>
          <el-form-item label="浦发联系人姓名">
            <el-input placeholder="请输入浦发联系人姓名"></el-input>
          </el-form-item>
          <el-form-item label="浦发联系人电话/邮箱">
            <el-input placeholder="请输入浦发联系人电话/邮箱"></el-input>
          </el-form-item>
        </div>
      </el-form>
      <div class="btn-wrap">
        <button @click="$router.push({name: 'certification'})">上一步</button>
        <button class="common-btn">下一步</button>
      </div>
    </div>
  </div>
</template>

<script>
import StepHeader from './step-header'
export default {
  data() {
    return {
      checked: false,
      stepIndex: 0,
      count: 5,
      disabled: false,
      yzmText:'获取验证码',
      stepList: [
        {
          title: "信息录入",
        },
        {
          title: "上传证件",
        },
        {
          title: "认证结果",
        },
      ],
      dataForm:{
        type: '1'
      }
    }
  },
  components:{
    StepHeader,
  },
  methods:{
    async getYzm(e) {
      let Timer = setInterval(() => {
        if(!this.count){
          this.yzmText ='获取验证码'
          this.count = 5
          this.disabled = false
          clearInterval(Timer)
          return
        }
        this.disabled = true
        this.yzmText = `获取验证码（${this.count}）`
        this.count --
      },1000)
    }
  }
}
</script>


<style lang="less" scoped>
.register-wrap {
  position: relative;
  padding: 30px 0;
  .wrap-center {
    width: 909px;
    margin: 0 auto;
    background: #fff;
    padding: 30px 58px 45px 58px;
    box-sizing: border-box;
    .title {
      color: #1A1A1A;
      font-size: 16px;
      font-weight: 500;
      text-align: center;
      margin-bottom: 32px;
      margin-top: 37px;
    }
    .dataForm {
      display: block;
      margin: 0 auto;
      .sub-title {
        font-size: 16px;
        color: #1A1A1A;
        font-weight: 500;
        padding: 14px 0;
        border-bottom: 1px dashed #DCDCDC;
        margin-bottom: 37px;
      }
      .form-step {
        padding-left: 50px;
        box-sizing: border-box;
      }
      .notSetLong /deep/ .el-input {
        width: 221px!important;
      }
      /deep/.el-input {
        width: 468px;
      }
      /deep/ .block-radio {
        .el-radio {
          display: block;
          margin-bottom: 10px;
        }
      }
      /deep/ .el-radio__input.is-checked+.el-radio__label {
        color: #B90909;
      }
      /deep/ .el-radio__input.is-checked .el-radio__inner {
        background-color: #B90909;
        border-color: #B90909;
      }
      /deep/ .el-form-item__label{
        color: #1F1E1E;
        font-weight: 500;
      }
      /deep/.el-checkbox .is-checked .el-checkbox__inner{
        background-color: #B90909;
        border-color: #B90909;
      }
      /deep/.el-checkbox__input.is-checked+.el-checkbox__label {
        color: #B90909;
      }
      .disabled {
        color: #ccc!important;
        border: 1px solid #ccc!important;;
      }
      .yzm {
        width: 150px;
        height: 40px;
        background: none;
        border: 1px solid #B90909;
        border-radius: 4px;
        color: #B90909;
      }
    }

    .btn-wrap {
      display: flex;
      justify-content: center;
      button {
        width: 169px;
        height: 40px;
        font-size: 14px;
        font-weight: 500;
        &:first-child {
          margin-right: 17px;
          background: #fff;
          border: 1px solid #BFBFBF;
          border-radius: 4px;
          color: #5E5E5E;
          font-size: 14px;
        }
      }
    }
  }
}
</style>


